const http = require('http');

module.exports  = class App {
    constructor(host,port) {
        this.host = host;
        this.port = port;
    }
    use(cb){
        this.server = http.createServer(cb);

    }

    start(port,host,cb){
        host = String(host)
        this.server.listen(port, host,cb);
    }
}


